import { capitalCase } from 'change-case';
import { useState, useEffect, } from 'react';
import { useParams, useLocation } from 'react-router-dom';
// @mui
import { Container,Backdrop,CircularProgress } from '@mui/material';
// routes
import { PATH_ADMIN} from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import UserNewForm from '../../sections/admin/UserNewForm';
import axios from '../../utils/axios';

export default function NewUser(){
    const { themeStretch } = useSettings();
    const { pathname } = useLocation();
    const { name = '' } = useParams();
    const isEdit = pathname.includes('edit');
    const [currentUser, SetCurrentUser] = useState();
    const [load,SetLoad] = useState(true);
    const AccessToken =  window.localStorage.getItem('accessToken');
    useEffect(()  => {
        const initialize = async () => {
        if(name){
       await axios.post(`/api/system/all/user/edit/user/safe/url/${AccessToken}/secure`,{
            username: name
        }).then(data => {
               SetCurrentUser(data.data.user); 
               SetLoad(false)
               }).catch(() => SetLoad(false));
            }else{
                SetCurrentUser([]);
                SetLoad(false)
            }
        }
        initialize();
        }, [AccessToken, name, SetCurrentUser]);  
    return (
      <Page title={"User: " && !isEdit ? 'Create a new user' : 'Edit user'}>
        <Container maxWidth={themeStretch ? false : 'lg'}>
          <HeaderBreadcrumbs
            heading={!isEdit ? 'Create a new user' : 'Edit user'}
            links={[
              { name: 'Dashboard', href: PATH_ADMIN.root },
              { name: 'User', href: PATH_ADMIN.user.all_user },
              { name: !isEdit ? 'New user' : capitalCase(name) },
            ]}
          />
  
          <UserNewForm isEdit={isEdit} currentUser={currentUser} />
          <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={load}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
        </Container>
      </Page>
    );
        }
